<!-- 登录/注册页面 -->
<template>
  <div class="main-body" :style="backgroundDiv">
    <el-card shadow="none" class="wish-card">
      <div class="header-logo">
        <el-image :src="logo" fit="fill" class="carousel-image">
          <div slot="error" class="image-slot">
            <i class="el-icon-picture-outline"></i>
          </div>
        </el-image>
      </div>
      <div v-show="showLogin" style="width: 100%;">
        <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="0">
          <el-form-item label="" prop="username">
            <el-input prefix-icon="el-icon-user-solid" v-model="ruleForm.username" placeholder="请输入用户名" ></el-input>
          </el-form-item>
          <el-form-item label="" prop="pass">
            <el-input prefix-icon="el-icon-s-cooperation" type="password" v-model="ruleForm.pass" autocomplete="off" placeholder="请输入密码"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" :loading="loading" class="submit-btn" @click="submitForm('ruleForm')">登录</el-button>
          </el-form-item>
          <el-form-item>
            <el-button type="info" :loading="loading" class="submit-btn" @click="showLogin = false">注册</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div v-show="!showLogin">
        <el-form :model="ruleForm2" status-icon :rules="rules" ref="ruleForm2" label-width="0">
          <el-form-item label="" prop="username">
            <el-input prefix-icon="el-icon-user-solid" v-model="ruleForm2.username" placeholder="请输入用户名"></el-input>
          </el-form-item>
          <el-form-item label="" prop="pass">
            <el-input prefix-icon="el-icon-s-cooperation" type="password" v-model="ruleForm2.pass" autocomplete="off" placeholder="请输入密码"></el-input>
          </el-form-item>
          <el-form-item label="" prop="rePass">
            <el-input prefix-icon="el-icon-s-cooperation" type="password" v-model="ruleForm2.rePass" autocomplete="off" placeholder="请再次输入密码"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" :loading="loading" class="submit-btn" @click="submitForm('ruleForm2')">注册</el-button>
          </el-form-item>
          <el-form-item>
            <el-button type="info" :loading="loading" class="submit-btn" @click="showLogin = true">返回</el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
  </div>
</template>

<script>
import { register } from '@/api/user'
import logo from '@/assets/logo.png'

export default {
  name: 'Login',
  watch: {
    $route: {
      handler (route) {
        this.redirect = route.query && route.query.redirect
      },
      immediate: true
    }
  },
  data () {
    var username = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('用户名不能为空'))
      }
      callback()
    }
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        if (this.ruleForm.checkPass !== '') {
          this.$refs.ruleForm.validateField('checkPass')
        }
        callback()
      }
    }
    var validateRePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else if (this.ruleForm2.pass !== this.ruleForm2.rePass) {
        callback(new Error('两次密码输入不同'))
      } else {
        if (this.ruleForm.checkPass !== '') {
          this.$refs.ruleForm.validateField('checkPass')
        }
        callback()
      }
    }
    return {
      showLogin: true,
      redirect: undefined,
      carouselHeight: '400px',
      backgroundDiv: {
        backgroundImage: 'url(' + require('../../assets/webapplogin.png') + ')'
      },
      logo: logo,
      loading: false,
      ruleForm: {
        pass: '',
        username: ''
      },
      ruleForm2: {
        pass: '',
        rePass: '',
        username: '',
        selectFamily: ''
      },
      rules: {
        pass: [
          { validator: validatePass, trigger: 'blur' }
        ],
        username: [
          { validator: username, trigger: 'blur' }
        ],
        rePass: [
          { validator: validateRePass, trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.loading = true
          if (this.showLogin) {
            const user = {
              username: this.ruleForm.username,
              password: this.ruleForm.pass
            }
            this.$store
              .dispatch('Login', user)
              .then(() => {
                this.getInfo()
              })
              .catch(() => {
                this.loading = false
                this.$message.error('用户名密码错误')
              })
          } else {
            this.userRegister()
          }
        } else {
          return false
        }
      })
    },
    async userRegister () {
      const data = {
        username: this.ruleForm2.username,
        password: this.ruleForm2.pass
      }
      await register(data)
        .then(() => {
          this.$message.success('注册成功！')
          this.showLogin = true
          this.loading = false
        })
        .catch(() => {
          this.$message.error('注册失败，请重试！')
          this.loading = false
        })
    },
    getInfo () {
      this.$store
        .dispatch('GetInfo')
        .then(() => {
          this.loading = false
          this.$router.replace({ path: this.redirect || '/index' })
        })
        .catch(() => {
          this.loading = false
          this.$message.error('获取个人信息失败')
        })
    }
  }
}

</script>
